import { useLoaderData } from "remix";
import Box from "~/components/BaseKit/Box/Box";
import {
  ColumnCards,
  links as ColumnCardsLinks,
} from "~/components/ColumnCards/ColumnCards";
import {
  LeftNavbar,
  links as LeftNavbarlinks
} from "~/components/Navigation/LeftNavbar/LeftNavbar";
import { Title, links as TitleLinks } from "~/components/Title/Title";
import { boards, cards } from "../services/trellotypes";

export function links() {
  return [...LeftNavbarlinks, ...TitleLinks, ...ColumnCardsLinks];
}

const factorSearch = (data, factor) => {
  return data.labels.find((ee) => ee.name === factor);
};
export let loader = async () => {
  // return await getListsOnBoard()

  let group = [
    {
      title: "INOVAÇÃO",
      data: [],
    },
    {
      title: "CORREÇÕES",
      data: [],
    },
  ];

  let trashColumn = boards.find((e) => e.name != "DESCARTADOS");
  let data = cards.filter((e) => e.idList != trashColumn.id);
  data.forEach((e) => {
    if (factorSearch(e, "INOVAÇÃO")) {
      group[0].data.push(e);
    } else if (factorSearch(e, "CORREÇÃO")) {
      group[1].data.push(e);
    }
  });
  return { data: group };
};

export default function Index() {
  let { data } = useLoaderData();
  return (
    <div
      style={{
        flexDirection: "row",
        display: "flex",
        height: "100%",
      }}
    >
      <LeftNavbar />
      <Box
        flexDirection="column"
        style={{
          padding: "30px",
        }}
      >
        <Title
        style={{
          marginBottom:"24px"
        }}
          title={
            <>
              Backlog Desenvolvimento <span style={{ color: "#EBEBED" }}></span>
              •{" "}
              <span
                style={{
                  color: "#7F7EFF",
                }}
              >
                Janeiro 2022
              </span>
            </>
          }
        ></Title>
        <Box flexDirection="row">
        {data?.map((e) => {
          return (<ColumnCards data={e} key={e.name} style={{marginRight:'50px'}}/>
            
          );
        })}
      </Box>
      </Box>
    </div>
  );
}
